import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/partner";

class PartnerMasterDataService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  
  getHeadersData = (uid: any = "") => {
    const url = `${DEFAULT_PATH}/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  save = (obj: any) => {
    const url = `${DEFAULT_PATH}/manage`;
    return this.makePostRequestWithAuth(url, obj);
  };
  update = (obj: any,uid:any) => {
    const url = `${DEFAULT_PATH}/manage/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  };
  activate = (uid: string) => {
    const url = `${DEFAULT_PATH}/manage/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  
  Inactivate = (uid: string) => {
    const url = `${DEFAULT_PATH}/manage/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  tableEmpanalment = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/empanelment/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tableAgreement = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/agreement/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tablecontact = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/contact/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tablekeyPerson = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/key/person/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tableReference = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/reference/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tableBankDetails = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/bank/detail/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tableRateCard = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/rate/card/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tableGstDetails = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/tax/details/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  tableOrgRepLevel = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/org/rep/level/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
 
  getChannelName = (start = 0, limit = 50, search="") => {
    const url = `${DEFAULT_PATH}/manage/filter?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new PartnerMasterDataService();
